import React from 'react'
import styles from './Cart.module.scss'
import CartItem from '../../components/CartItem.tsx'
import { Link } from "react-router-dom"
import { useSelector } from 'react-redux'
import { RootState } from '../../redux/store.ts'

const Cart: React.FC = () => {
    const {items} = useSelector((state: RootState) => state.cart)
    const totalPrice = items.reduce((sum, item) => sum + item.count * item.price, 0)
    return (
        <div className={styles.cart}>
        {items.length === 0 &&
        <div className={styles.cart_empty}>
            <h1 className={styles.cart_empty_headline}> !</h1>
            <Link to='/#' className={styles.cart_empty_button}> </Link>
        </div>
         }
        <div className={styles.cart_content}>
            <div className={styles.cart_items}>
                {items.map((item) => {
                    return <CartItem key = {item.name} {...item}/>
                })}
            </div>
            {items.length > 0 && 
            <div className={styles.cart_right}>
                <h2 className = {styles.cart_right_headline}> : <br/><span>{totalPrice} </span></h2>
                <a className = {styles.cart_right_button}> </a>
            </div>}
        </div>
        </div>
    )
}

export default Cart